import { useState } from "react"
import { useDispatch } from 'react-redux'
import { StreetPreview } from './street-preview.jsx'
import { useTranslation } from 'react-i18next'
import { updateUser } from '../store/actions/user.action';


export const ZonePreview = ({ idx, user, zones, zone }) => {
   console.log('zone:', zone)
   const [isNameEdit, setIsNameEdit] = useState(false)
   const [isCityEdit, setIsCityEdit] = useState(false)
   const [name, setName] = useState(zone.name)
   const [city, setCity] = useState(zone.city)
   const dispatch = useDispatch()
   const { t, i18n } = useTranslation();


   const handleChange = (ev) => {
      const field = ev.target?.name
      const value = ev.target?.value
      console.log('value:', value)

      switch (field) {
         case 'name':
            setName(value)
            break
         case 'city':
            setCity(value)
            break
      }
   }

   const onAdd = async (ev) => {
      ev.stopPropagation()
      ev.preventDefault()
   }
   const onSave = async (ev, type) => {
      ev.stopPropagation()
      ev.preventDefault()
      var newZones = [...zones]
      var newZone = { ...zone }
      newZones[idx] = newZone
      switch (type) {
         case 'name':
            if (!name) return
            newZone.name = name
            setIsNameEdit(false)
            break
         case 'city':
            if (!city) return
            newZone.city = city
            setIsCityEdit(false)
            break
      }
      var newUser = { ...user }
      newUser.zones = newZones
      await dispatch(updateUser(newUser))
      console.log('save')
   }

   return (
      <li className='zone-preview flex column align-center' >
         {(!isNameEdit) && (
            <div className="zone-name-preview" onClick={() => { setIsNameEdit(true) }}>
               <h2>{zone.name}</h2>
            </div>
         )}
         {(isNameEdit) && (
            <form className="zone-name-edit  flex align-center" style={{ width: '100%' }} onSubmit={onSave}>
               <input autoComplete='off' onChange={handleChange} autoFocus value={name} placeholder={t('Zone name')} type='text' name='name' />
               <button className="save-btn" onClick={(ev) => onSave(ev, 'name')}>Save</button>
               <button className="close-btn" onClick={() => { setIsNameEdit(false) }}>x</button>
            </form>
         )}

         {(!isCityEdit) && (<div className="city-name-preview flex align-center" onClick={() => { setIsCityEdit(true) }}>
            <h2>{t('City')}:
            </h2>
            <h3>
               {zone.city}
            </h3>
         </div>)}
         {(isCityEdit) && (<form className="city-name-edit flex align-center">
            <input autoComplete='off' onChange={handleChange} autoFocus value={city} placeholder={t('City name')} type='text' name='city' />
            <button className="save-btn" onClick={(ev) => onSave(ev, 'city')}>Save</button>
            <button className="close-btn" onClick={() => { setIsCityEdit(false) }}>x</button>
         </form>)}
         <hr />
         <h2>{t('Streets')}:</h2>
         {zone.streets.map((street, idx) => {
            return (
               <StreetPreview key={idx} idx={idx} street={street} />
            )
         })}
         <button onClick={(ev) => onAdd(ev)}>{t('Add')}</button>

      </li>
   )
}
